/**
 * src\views\super_market\mark\bargains\add_bargains\add_bargains.vue
 */
const MIXINS_SUPER_BARGAINS_AND_TEAM_BUY_DATA = {
  data() {
    const checkDiscount = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''))
      }
      if (!Number.isInteger(value)) {
        return callback(new Error(''))
      } else if (value > 100 || value < 1) {
        return callback(new Error('1001'))
      }
    }
    return {
      activeType: 1, //  0- 1- 2-
      timeDate: '', // 
      time: '', // 
      form: {
        secondsStartTime: '', // 
        secondsStopTime: '', // 
        whenLength: 4, //  4
        activeDescribes: '', // 
        secondsKillName: '', // /
        discount: '', //  
      },
      rules: {
        secondsKillName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        secondsStartTime: [
          { required: true, message: '', trigger: 'change' }
        ],
        whenLength: [
          { required: true, message: '', trigger: 'blur' }
        ],
        activeDescribes: [
          { required: true, message: '', trigger: 'blur' }
        ],
        discount: [
          { validator: checkDiscount, trigger: 'blur' }
        ]
      },
      goodsTotle: 0, // 
      oldUpdateIndex: '', // 
      updateIndex: '', // 
      dialogVisible: false, // 
      goodsAndActivity: [], // 
      blUpdata: false, // 
      oldGoodsAndActivity: [] // 
    }
  },
}

export {
  MIXINS_BARGAINS_AND_TEAM_BUY_DATA,
  MIXINS_BTNPERMISSIONS_DATA,
  MIXINS_SUPER_BARGAINS_AND_TEAM_BUY_DATA,
}
