<template>
  <div>
    <layout-box one="" two="">
      <el-card shadow="never" class="mb-1">
        <div>
          <i class="el-icon-search"></i>
          <span></span>
          <el-button style="float: right" type="primary" size="small" @click="handleSearchList()"></el-button>
          <el-button style="float:right;margin-right: 15px" size="small" @click="handleResetSearch"></el-button>
        </div>
        <div class="mt-1">
          <el-form inline size="small" label-width="140px" :model="queryInfo">
            <el-form-item label="">
              <el-input v-model="queryInfo.name" style="width: 203px" placeholder="" clearable @clear="fetch"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="queryInfo.code" style="width: 203px" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="queryInfo.state" placeholder="">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="0"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="queryInfo.approvalStatus">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="0"></el-option>
                <el-option label="" value="2"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </div>
      </el-card>
      <router-link v-if="btnPermissions.indexOf('') !== -1" to="/commo/add">
        <el-button type="danger" size="medium" icon="el-icon-plus"></el-button>
      </router-link>
      <div v-if="btnPermissions.indexOf('') !== -1" class="ml-1" style="float: right">
        <!-- <el-button type="danger" class="ml-1" size="medium" :disabled="sels.length === 0" @click="deleteFileOrDirectory(sels)"></el-button> -->
        <el-dropdown v-if="HomePageClassificationQueryList.length !== 0" class="mb-1" trigger="click" @command="unHandleCommand">
          <el-button size="medium">
            <i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown" style="width: 100px">
            <el-dropdown-item :key="1" :command="1"></el-dropdown-item>
            <el-dropdown-item v-for="item in HomePageClassificationQueryList" :key="item.id" :command="item.id">{{ item.name }}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div v-if="btnPermissions.indexOf('') !== -1" style="float: right">
        <el-dropdown v-if="HomePageClassificationQueryList.length !== 0" class="mb-1" trigger="click" @command="setZiYingTeJiaGoodsCommand">
          <el-button size="medium">
            <i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown" style="width: 130px">
            <el-dropdown-item :command="0"></el-dropdown-item>
            <el-dropdown-item :command="1"></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div style="float: right">
        <el-button v-if="btnPermissions.indexOf('') !== -1" class="mb-1 mr-1" size="medium" @click="$router.push('/commodity/set_cai_dan')"></el-button>
      </div>
      <div style="float: right">
        <el-button v-if="btnPermissions.indexOf('') !== -1" class="mb-1 mr-1" size="medium" @click="setShowcase"></el-button>
      </div>
      <div style="float: right">
        <el-button v-if="btnPermissions.indexOf('') !== -1" class="mb-1 mr-1" size="medium" @click="setSort"></el-button>
      </div>
      <div v-if="btnPermissions.indexOf('') !== -1" style="float: right">
        <!-- <el-button type="danger" class="ml-1" size="medium" :disabled="sels.length === 0" @click="deleteFileOrDirectory(sels)"></el-button> -->
        <el-dropdown v-if="HomePageClassificationQueryList.length !== 0" class="mb-1 mr-1" trigger="click" @command="handleCommand">
          <el-button size="medium">
            <i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown" style="width: 100px">
            <el-dropdown-item :key="1" :command="1"></el-dropdown-item>
            <el-dropdown-item v-for="item in HomePageClassificationQueryList" :key="item.id" :command="item.id">{{ item.name }}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div style="float: right">
        <el-button v-if="btnPermissions.indexOf('') !== -1" class="mb-1 mr-1" size="medium" @click="setJiaDianGoodssVisible = true"></el-button>
      </div>
      <!--  -->
      <el-table v-loading="loading" class="mt-2" border :data="goodslist" style="width: 100%" max-height="500" @selection-change="selsChange">
        <el-table-column type="selection" width="40" align="center" />
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <p>{{ scope.row.sorting }}</p>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="" width="125" align="center">
          <template slot-scope="scope">
            <el-image style="width: 100px; height: 100px" :src="scope.row.imageUrl" />
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <p>{{ scope.row.name }}</p>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            {{ scope.row.categoryName }}
          </template>
        </el-table-column>
        <el-table-column align="center" label="">
          <template slot-scope="scope">
            {{ scope.row.sales || 0 }}
          </template>
        </el-table-column>
        <el-table-column v-if="btnPermissions.indexOf('') !== -1" align="center" label="">
          <template slot-scope="scope">
            {{ scope.row.virtualAmount || 0 }}
          </template>
        </el-table-column>
        <el-table-column v-if="btnPermissions.indexOf('') !== -1" align="center" label="">
          <template slot-scope="scope">
            {{ scope.row.homeVideo === 1 ? '' : '' }}
          </template>
        </el-table-column>
        <template v-if="btnPermissions.indexOf('') !== -1">
          <el-table-column v-for="item in HomePageClassificationQueryList" :key="item.id" align="center" :label="item.name">
            <template slot-scope="scope">
              <div v-for="listNameItem in scope.row.listName" :key="listNameItem.id">
                <i v-if="listNameItem.id === item.id" class="iconfont icon">&#xe605;</i>
              </div>
            </template>
          </el-table-column>
        </template>
        <el-table-column align="center" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.state === 1" style="color: #40ccae"></span><span
              v-else-if="scope.row.state === 0"
              style="color: #e39025"
            ></span>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <span
              v-if="scope.row.approvalStatus === 0"
              style="color: #666666"
            ></span>
            <span
              v-if="scope.row.approvalStatus === 1"
              style="color: #40ccae"
            ></span>
            <span
              v-if="scope.row.approvalStatus === 2"
              style="color: #e02929"
            ></span>
          </template>
        </el-table-column>
        <el-table-column v-if="btnPermissions.indexOf('') !== -1" label="" align="center">
          <template slot-scope="scope">
            <p>
              
              <el-switch
                v-model="scope.row.state"
                :disabled="scope.row.approvalStatus == '0'"
                :active-value="1"
                :inactive-value="0"
                @change="handlePublishStatusChange(scope.id, scope.row)"
              />
            </p>
          </template>
        </el-table-column>
        <el-table-column prop="address" align="center" label="" width="179">
          <template slot-scope="scope">
            <el-link
              v-if="btnPermissions.indexOf('') !== -1"
              :underline="false"
              type="primary"
              @click="onEdit(scope)"
            >
              
            </el-link>
            <el-link
              type="success"
              :underline="false"
              @click="onDetail(scope)"
            >
              
            </el-link>
            <el-link
              v-if="btnPermissions.indexOf('') !== -1"
              type="danger"
              :underline="false"
              class="text-white"
              @click="removeUserById(scope.row.id)"
            ></el-link>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        class="mt-1"
        :current-page.sync="queryInfo.pageNo"
        :page-sizes="[2, 5, 10, 20]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </layout-box>

    <el-dialog
      title=""
      :visible.sync="salesDialogVisible"
      width="30%"
    >
      <span></span>
      <el-input v-model="amount"></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="salesDialogVisible = false"> </el-button>
        <el-button type="primary" @click="onSalesSubmit"> </el-button>
      </span>
    </el-dialog>

    <el-dialog title="" :visible.sync="selectDialogFormVisible">
      <el-form :model="sortingForm">
        <el-form-item label="">
          <el-input v-model="sortingForm.sorting"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="selectDialogFormVisible = false"></el-button>
        <el-button type="primary" @click="onSaveSort"></el-button>
      </div>
    </el-dialog>

    <el-dialog title="" :visible.sync="selectDialogFormVisible">
      <el-form :model="sortingForm">
        <el-form-item label="">
          <el-input v-model="sortingForm.sorting"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="selectDialogFormVisible = false"></el-button>
        <el-button type="primary" @click="onSaveSort"></el-button>
      </div>
    </el-dialog>

    <setZiYingTeJiaGoods-cmp v-if="setZiYingTeJiaGoodsVisible" :dialogTableVisible="setZiYingTeJiaGoodsVisible" @close="onClose" />
    <setJiaDianGoods-cmp :dialogVisible="setJiaDianGoodssVisible" @close="onCloseSetJiaDianGoodss" />
  </div>
</template>

<script>
import { MIXINS_BTNPERMISSIONS_DATA } from '@/mixins/data.js'
import { MIXINS_APIPRODUCTQUERYCHOOSE_API, MIXINS_PRODUCTBATCHVIP_API } from '@/mixins/api.js'
import setZiYingTeJiaGoodsCmp from './cmp/set_zi_ying_te_jia_goods.vue'
import setJiaDianGoodsCmp from './cmp/set_jia_dian_goods.vue'

const defaultListQuery = {
  pageNo: 1,
  pageSize: 10,
  name: null,
  code: null,
  state: null,
  approvalStatus: null
}
export default {
  data() {
    return {
      sels: [], // 
      model: {},
      approve: {
        approveReason: '',
        approveStatus: 0
      },
      examine: false,
      editSkuInfo: {
        dialogVisible: false,
        productId: null,
        productSn: '',
        productAttributeCategoryId: null,
        stockList: [],
        productAttr: [],
        keyword: null
      },
      // 
      loading: true,
      // 
      query: '',
      queryInfo: Object.assign({}, defaultListQuery),
      // 
      goodslist: [],
      state: null,
      // 
      total: 0,
      // 0- 1- 2-
      queryInf: {
        approveStatus: 1,
        id: ''
      },
      queryInf1: {
        approveStatus: 2,
        id: ''
      },
      HomePageClassificationQueryList: [], // 
      sortingForm: {
        sorting: '', // 
      },

      // ------------------------------------------------------
      // 
      salesDialogVisible: false, // 
      amount: '', // 
      selectDialogFormVisible: false, // 
      setZiYingTeJiaGoodsVisible: false, // 
      setJiaDianGoodssVisible: false, // 
    }
  },

  components: {
    setZiYingTeJiaGoodsCmp,
    setJiaDianGoodsCmp
  },

  mixins: [
    MIXINS_BTNPERMISSIONS_DATA,
    MIXINS_APIPRODUCTQUERYCHOOSE_API,
    MIXINS_PRODUCTBATCHVIP_API,
  ],

  methods: {
    init () {
      this.fetch()
      // this.productQueryChoose()
    },
    // 
    onEdit (scope) {
      var { href } = this.$router.resolve({
        path: `/commo/edit/${scope.row.id}`,
      })
      window.open(href)
    },
    // 
    onDetail (scope) {
      var { href } = this.$router.resolve({
        path: `/commo/audit/${scope.row.id}`,
      })
      window.open(href)
    },
    // 
    handleShowVerifyDetail(id) {
      this.examine = true
      this.queryInf.id = id
      this.queryInf1.id = id
    },
    //  
    fetch () {
      this.loading = true
      this.queryInfo.type = 0
      this.$http.post(this.$api.productQuery, this.queryInfo).then(res => {
        this.loading = false
        this.goodslist = res.data.data.list
        this.total = res.data.data.total
      })
    },
    handleSizeChange(pageSize) {
      this.queryInfo.pageSize = pageSize
      this.fetch()
    },
    handleCurrentChange(pageNo) {
      this.queryInfo.pageNo = pageNo
      this.fetch()
    },
    // 
    handlePublishStatusChange(id, row) {
      this.$http
        .post(`/woho/product/head/upOrDown/${row.id}/${row.state}`)
        .then(res => {
          if (res.data.code !== 200) return this.$message.error('')
          this.$message.success('')
          this.fetch()
        })
    },
    // 
    removeUserById(id) {
      this.$confirm(` ""`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$http.delete(`/woho/product/head/delete/` + id).then(res => {
          this.$message({
            type: 'success',
            message: '!'
          })
          this.fetch()
        })
      })
    },
    // 
    handleSearchList() {
      this.queryInfo.pageNo = 1
      this.fetch()
    },
    handleResetSearch() {
      this.queryInfo = Object.assign({}, defaultListQuery)
      this.fetch()
    },
    // 
    selsChange(sels) {
      this.sels = sels
    },
    deleteFileOrDirectory() {
      let arr = this.sels.map(item => item.id).join()// id
      console.log(arr)
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        const ids = arr
        this.$http.delete(`/woho/product/head/batchDelete/` + ids).then(res => {
          this.$message({
            message: '',
            type: 'success',
            duration: 1000
          })
          this.fetch()
        })
      })
    },
    // vip
    async handleCommand (command) {
      if (this.sels.length === 0) return this.$message.error('')
      if (command === 1) {
        this.salesDialogVisible = true
      } else {
        await this.apiProductBatchChooseApi(command)
      }
    },
    // 
    async unHandleCommand (command) {
      if (this.sels.length === 0) return this.$message.error('')
      if (command === 1) {
        const prodId = this.sels.map((item) => item.id)
        const res = await this.$http.get(`${this.$api.updateVirtual}/${prodId.join(',')}/0`)
        if (res.data.success) {
          this.$message.success('')
          this.fetch()
        } else {
          this.$message.error(res.data.msg)
        }
      } else {
        await this.apiUpdateClassificationProd(command)
      }
    },
    // 
    async setShowcase () {
      if (this.sels.length === 0) return this.$message.error('')
      console.log(this.sels.length)
      if (this.sels.length > 1) return this.$message.error('')
      let prodId = ''
      this.sels.forEach((item) => {
        prodId = item.id
      })
      const res = await this.$http.get(`${this.$api.updateprodVido}/${prodId}`)
      if (res.data.success) {
        this.$message.success('')
        this.fetch()
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 
    async onSalesSubmit () {
      const prodId = this.sels.map((item) => item.id)
      const res = await this.$http.get(`${this.$api.updateVirtual}/${prodId.join(',')}/${this.amount}`)
      if (res.data.success) {
        this.$message.success('')
        this.salesDialogVisible = false
        this.fetch()
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 
    setSort () {
      if (this.sels.length === 0) return this.$message.error('')
      if (this.sels.length > 1) return this.$message.error('')
      this.selectDialogFormVisible = true
    },
    // 
    async onSaveSort () {
      const prodId = this.sels[0].id
      const res = await this.$http.get(`${this.$api.updateSorting}/${prodId}/${this.sortingForm.sorting}`)
      if (res.data.success) {
        this.fetch()
        this.selectDialogFormVisible = false
        this.$message.success('')
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 
    async setZiYingTeJiaGoodsCommand (e) {
      if (e === 1) {
        this.setZiYingTeJiaGoodsVisible = true
      } else {
        if (this.sels.length === 0) return this.$message.error('')
        const prodId = this.sels.map((item) => item.id)
        await this.apiOptimizationProd(prodId.join(','))
      }
    },
    // 
    onClose () {
      this.setZiYingTeJiaGoodsVisible = false
    },
    // 
    onCloseSetJiaDianGoodss () {
      this.setJiaDianGoodssVisible = false
    },

    // --------------------------------------------
    // 
    async apiProductBatchChooseApi (classificationId) {
      const prodId = this.sels.map((item) => item.id)
      const data = {
        prodId: prodId.join(','),
        classificationId: classificationId
      }
      const res = await this.$http.post(this.$api.addClassificationProd, data)
      if (res.data.success) {
        this.$message.success('')
        this.fetch()
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 
    async apiUpdateClassificationProd (classificationId) {
      const prodId = this.sels.map((item) => item.id)
      const res = await this.$http.get(`${this.$api.updateClassificationProd}/${classificationId}/${prodId}`)
      if (res.data.success) {
        this.$message.success('')
        this.fetch()
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 
    async apiHomePageClassificationQuery () {
      const data = {
        pageNo: 1,
        pageSize: 999,
        enableStatus: 0, // 
      }
      const res = await this.$http.post(this.$api.HomePageClassificationQuery, data)
      this.HomePageClassificationQueryList = res.data.data.list
    },
    // 
    async apiOptimizationProd (prodId) {
      const data = {
        prodIds: prodId,
        type: 0, // 0, 1
      }
      const res = await this.$http.post(this.$api.optimizationProd, data)
      if (res.data.success) {
        this.$message.success('')
        this.fetch()
      } else {
        this.$message.error(res.data.msg)
      }
    },
  },

  created() {
    this.init()
    this.apiHomePageClassificationQuery()
  },
}
</script>

<style lang="scss" scoped>
.el-link {
  display: inline-block;
  width: 25%;
}

.box-card {
  min-width: 1300px;
}
.btns {
  color: #fff;
}
.iconfont{
  color: #ff9f00;
  font-size: 25px;
}
</style>
